'use client';

import React from 'react';
import { Button, Flex, Space } from 'antd';
import {
    UndoOutlined,
    CloseOutlined,
    HeartFilled,
    StarFilled,
    ThunderboltFilled
} from '@ant-design/icons';

interface ActionButtonsProps {
    onUndo: () => void;
    onPass: () => void;
    onSuperLike: () => void;
    onLike: () => void;
    onBoost: () => void;
    canUndo: boolean;
    hasCard: boolean;
}

export function ActionButtons({
    onUndo,
    onPass,
    onSuperLike,
    onLike,
    onBoost,
    canUndo,
    hasCard
}: ActionButtonsProps) {
    if (!hasCard) return null;

    const containerStyle: React.CSSProperties = {
        position: 'absolute',
        bottom: 16,
        left: 0,
        right: 0,
        padding: '12px 16px',
        display: 'flex',
        justifyContent: 'center',
        // background: 'linear-gradient(to top, rgba(0,0,0,0.45) 0%, transparent 100%)',
        zIndex: 10,
    };

    const buttonGroupStyle: React.CSSProperties = {
        width: '100%',
        // maxWidth: 400,
        display: 'flex',
        justifyContent: 'space-around',
        alignItems: 'center',
    };

    const baseButtonStyle: React.CSSProperties = {
        borderRadius: '50%',
        boxShadow: '0 4px 16px rgba(0,0,0,0.25)',
        border: 'none',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        transition: 'transform 0.15s ease',
    };

    // Responsive sizing (mobile vs desktop)
    const isMobile = typeof window !== 'undefined' && window.innerWidth < 768;

    const smallButtonStyle: React.CSSProperties = {
        ...baseButtonStyle,
        width: isMobile ? 46 : 52,
        height: isMobile ? 46 : 52,
    };

    const largeButtonStyle: React.CSSProperties = {
        ...baseButtonStyle,
        width: isMobile ? 58 : 66,
        height: isMobile ? 58 : 66,
    };

    const iconSize = isMobile ? 20 : 24;

    return (
        <div style={containerStyle}>
            <div style={buttonGroupStyle}>
                {/* Undo */}
                <Button
                    style={{
                        ...smallButtonStyle,
                        backgroundColor: '#ffffff',
                        opacity: canUndo ? 1 : 0.5,
                    }}
                    onClick={onUndo}
                    disabled={!canUndo}
                    icon={<UndoOutlined style={{ color: '#666', fontSize: iconSize }} />}
                />

                {/* Pass */}
                <Button
                    style={{
                        ...largeButtonStyle,
                        backgroundColor: '#ffffff',
                    }}
                    onClick={onPass}
                    icon={<CloseOutlined style={{ color: '#ff4d4f', fontSize: iconSize + 4 }} />}
                />

                {/* Super Like */}
                <Button
                    style={{
                        ...smallButtonStyle,
                        backgroundColor: '#ffffff',
                    }}
                    onClick={onSuperLike}
                    icon={<StarFilled style={{ color: '#1890ff', fontSize: iconSize }} />}
                />

                {/* Like */}
                <Button
                    style={{
                        ...largeButtonStyle,
                        backgroundColor: '#52c41a',
                    }}
                    onClick={onLike}
                    icon={<HeartFilled style={{ color: '#fff', fontSize: iconSize + 4 }} />}
                />

                {/* Boost */}
                <Button
                    style={{
                        ...smallButtonStyle,
                        backgroundColor: '#ffffff',
                    }}
                    onClick={onBoost}
                    icon={<ThunderboltFilled style={{ color: '#722ed1', fontSize: iconSize }} />}
                />
            </div>
        </div>
    );
}
