'use client';

import React from 'react';
import { SwipeCard } from './SwipeCard';
import { SwipeCardItem } from '../../data/swipeData';

interface SwipeCardStackProps {
    cards: SwipeCardItem[];
    onSwipe: (direction: 'left' | 'right') => void;
    triggerSwipe?: 'left' | 'right' | null;
    onSwipeComplete?: () => void;
}

export function SwipeCardStack({ cards, onSwipe, triggerSwipe, onSwipeComplete, }: SwipeCardStackProps) {
    if (cards.length === 0) return null;

    const containerStyle: React.CSSProperties = {
        position: 'relative',
        width: '100%',
        height: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    };

    return (
        <div style={containerStyle}>
            {/* Render top 3 cards for performance */}
            {cards
                .slice(-3)
                .reverse()
                .map((card, index) => {
                    const isTopCard = index === 0;

                    // Layered card styling
                    const cardStyle: React.CSSProperties = {
                        position: 'absolute',
                        top: '50%',
                        left: '50%',
                        transform: `translate(-50%, -50%) scale(${1 - index * 0.05}) rotate(${index * 1.5}deg)`,
                        zIndex: 50 - index,
                        opacity: isTopCard ? 1 : 0.85,
                        width: '90%',
                        maxWidth: '400px',
                        height: '80%',
                        maxHeight: '600px',
                        borderRadius: '16px',
                        boxShadow: '0 8px 32px rgba(0, 0, 0, 0.35)',
                        backgroundColor: '#000', // fallback if image fails
                        overflow: 'hidden',
                        transition: 'transform 0.3s ease, opacity 0.3s ease',
                    };

                    return (
                        <SwipeCard
                            key={`${card.id}-${cards.length}`} // force re-render when new card added
                            card={card}
                            onSwipe={onSwipe}
                            style={cardStyle}
                            triggerSwipe={isTopCard ? triggerSwipe : null}
                            onSwipeComplete={onSwipeComplete}
                            isTopCard={isTopCard}
                        />
                    );
                })}
        </div>
    );
}


